button,
.btn {
    padding: calc(var(--main-padding) / 2) var(--ligt-padding);
    background-color: var(--light-color);
    border-radius: 6px;
    cursor: pointer;
    color: inherit;
    border: none;

    &.danger {
        background-color: red;
        color: white;
    }

    &.accept {
        background-color: green;
        color: white;
    }

    &.primary {
        font-size: 17px;
        padding: 10px 20px;
    }

    &.secondary {
        font-size: 17px;
        padding: 10px 20px;
        background-color: var(--main-color);
        color: white;
    }
}

.icon {
    background-color: transparent;
    box-shadow: none;
    padding: 5px;
}